<template>
  <div class="tab">
    <router-link tag="div" class="tab-item" to="/upload">
      <span class="tab-link">上传文件</span>
    </router-link>
    <router-link tag="div" class="tab-item" to="/video">
      <span class="tab-link">电影</span>
    </router-link>
    <router-link tag="div" class="tab-item" to="/">
      <span class="tab-link">主页
      </span>
    </router-link>
    <router-link tag="div" class="tab-item" to="/chat">
      <span class="tab-link">聊天</span>
    </router-link>
  </div>
</template>

<script type="text/ecmascript-6">
export default {}
</script>

<style scoped lang="scss" >
.tab {
  display: flex;
  height: 44px;
  line-height: 44px;
  font-size: 2vh;
  .tab-item {
    flex: 1;
    text-align: center;
    .tab-link {
      padding-bottom: 5px;
      color: orange;
    }
  }
  &.router-link-active {
    .tab-link {
      color: pink;
      border-bottom: 2px solid orange;
    }
  }
}
</style>



